<template>
  <div class="create-page">
    <the-navbar />
    <main class="main mt-5 pt-5">
      <div class="container">
        <div class="row">
          <div class="col-12 col-lg-9">
            <div class="card border-0 shadow-sm mb-4">
              <div class="card-header text-center border-0 p-3 bg-white">
                <h1 class="card-title m-0 p-0">Crear sitio</h1>
              </div>
              <div class="card-body p-3">
                <form @submit.prevent="onSubmit">
                  <div class="form-group">
                    <label for="name">Nombre del sitio *</label>
                    <input
                      type="text"
                      class="form-control"
                      v-model="form.name"
                      id="name"
                      placeholder="Nombre del sitio"
                      required
                      autofocus
                    />
                  </div>
                  <div class="form-group">
                    <label for="slug">Link *</label>
                    <input
                      type="text"
                      class="form-control"
                      v-model="form.slug"
                      id="slug"
                      placeholder="link"
                      required
                    />
                  </div>
                  <div class="form-row">
                    <div class="form-group col-md-6">
                      <label for="geoLat">Latitud</label>
                      <input
                        type="text"
                        class="form-control"
                        v-model="form.geo.geoLat"
                        id="geoLat"
                      />
                    </div>
                    <div class="form-group col-md-6">
                      <label for="geoLon">Longitud</label>
                      <input
                        type="text"
                        class="form-control"
                        v-model="form.geo.geoLon"
                        id="geoLon"
                      />
                    </div>
                  </div>
                  <div class="form-group">
                    <label for="image">Imagen</label>
                    <input
                      type="text"
                      class="form-control"
                      v-model="form.image"
                      id="image"
                      placeholder="Imagen"
                    />
                  </div>
                  <div class="form-group">
                    <label for="description">Descripción</label>
                    <textarea
                      class="form-control"
                      v-model="form.description"
                      id="description"
                      rows="3"
                      placeholder="Descripción"
                    ></textarea>
                  </div>
                  <div class="form-group">
                    <label for="location">Dirección</label>
                    <input
                      type="text"
                      class="form-control"
                      v-model="form.location"
                      id="location"
                      placeholder="Dirección"
                    />
                  </div>
                  <div class="form-group">
                    <label for="phone">Teléfono</label>
                    <input
                      type="text"
                      class="form-control"
                      v-model="form.phone"
                      id="phone"
                      placeholder="Teléfono"
                    />
                  </div>
                  <div class="form-group">
                    <label for="mobile">Celular</label>
                    <input
                      type="text"
                      class="form-control"
                      v-model="form.mobile"
                      id="mobile"
                      placeholder="Celular"
                    />
                  </div>
                  <div class="form-group">
                    <label for="countries">País *</label>
                    <select
                      class="form-control"
                      v-model="form.country"
                      id="countries"
                      required
                      @change="changedCountry()"
                    >
                      <option
                        v-for="(country, index) in getCountries"
                        :key="index"
                        :value="country.slug"
                        >{{ country.name }}</option
                      >
                    </select>
                  </div>
                  <div class="form-group">
                    <label for="countries">Estado</label>
                    <select
                      class="form-control"
                      v-model="form.state"
                      id="states"
                      @change="changedState()"
                    >
                      <option
                        v-for="(state, index) in getStates"
                        :key="index"
                        :value="state.code"
                        >{{ state.name }}</option
                      >
                    </select>
                  </div>
                  <div class="form-group">
                    <label for="countries">Ciudad</label>
                    <select class="form-control" v-model="form.city" id="city">
                      <option
                        v-for="(city, index) in getCities"
                        :key="index"
                        :value="city.code"
                        >{{ city.name }}</option
                      >
                    </select>
                  </div>
                  <button type="submit" class="btn btn-primary btn-block">
                    Crear
                  </button>
                </form>
              </div>
            </div>
          </div>
          <div class="col-12 col-lg-3">
            <div class="row">
              <div class="col-12 col-sm-4 col-lg-12">
                <Adds />
              </div>
              <div class="col-12 col-sm-4 col-lg-12">
                <Featureds />
              </div>
              <div class="col-12 col-sm-4 col-lg-12">
                <Adds />
              </div>
              <div class="col-12">
                <the-footer />
              </div>
            </div>
          </div>
        </div>
      </div>
    </main>
    <pre class="container">{{ $data }}</pre>
  </div>
</template>

<script>
import { mapActions, mapGetters, mapState } from "vuex";

import Adds from "@/components/Adds";
import Featureds from "@/components/Featureds";
import TheFooter from "@/components/TheFooter";
import TheNavbar from "@/components/TheNavbar";

export default {
  name: "CreatePage",
  components: {
    Adds,
    Featureds,
    TheFooter,
    TheNavbar
  },
  data() {
    return {
      form: {
        name: "",
        slug: "",
        userId: this.userAuth.uid,
        geo: {
          geoLat: "",
          geoLon: ""
        },
        image: "",
        description: "",
        location: "",
        phone: "",
        mobile: "",
        city: "",
        country: "",
        state: ""
      }
    };
  },
  created() {
    this.fetchCountries();
  },
  methods: {
    ...mapState(["userAuth"]),
    ...mapActions(["addPage", "fetchCountries", "fetchStates", "fetchCities"]),
    onSubmit() {
      this.addPage(this.form);
    },
    async changedCountry() {
      this.fetchStates(this.form.country);
    },
    async changedState() {
      this.fetchCities(this.form.state);
    }
  },
  computed: {
    ...mapGetters(["getCountries", "getStates", "getCities"])
  }
};
</script>

<style scoped></style>
